.review-section {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 40px 20px;
    background: linear-gradient(to right, #f6f8fc, #ffffff);
    transition: all 0.3s ease-in-out;
    padding-bottom: 50px;
}

/* Text */
.rw-text-content {
    max-width: 700px;
    text-align: center;
}

.rw-text-title {
    font-size: 28px;
    font-weight: 600;
    color: #4a4a4a;
    margin-bottom: 10px;
}

.rw-text-num {
    color: #795484;
    font-weight: 700;
}

.rw-text-desc {
    font-size: 32px;
    font-weight: bold;
    color: #222;
    margin-bottom: 20px;
}

.rw-text-format {
    position: relative;
    font-size: 20px;
    line-height: 1.6;
    padding: 20px;
    background: #f3effb;
    border-radius: 10px;
    font-style: italic;
    transition: opacity 0.4s ease-in-out;
    
    /* Fixed height for consistent layout */
    height: 150px; /* Adjust based on longest review */
    max-width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    overflow: hidden;
}

/* Ensure the text doesn't overflow */
.rw-review {
    max-height: 120px; /* Adjust to fit inside rw-text-format */
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: normal; /* Allows wrapping */
    display: -webkit-box;
    -webkit-line-clamp: 4; /* Limits to 4 lines */
    -webkit-box-orient: vertical;
}


.rw-text-quote1, .rw-text-quote2 {
    font-size: 40px;
    font-weight: bold;
    color: #795484;
    position: absolute;
}

.rw-text-quote1 {
    top: -10px;
    left: -7px;
}

.rw-text-quote2 {
    bottom: -10px;
    right: -2px;
}
/* Author */
.rw-authors {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 20px;
}

.rw-reviewer-name {
    font-size: 22px;
    font-weight: 700;
    color: #333;
}

.rw-reviewer-place {
    font-size: 18px;
    color: #6f7074;
}

/* Buttons */
.rw-nav-btn {
    font-size: 32px;
    background: none;
    border: none;
    cursor: pointer;
    color: #795484;
    transition: transform 0.2s ease-in-out;
}

.rw-nav-btn:hover {
    transform: scale(1.2);
}

/* Responsive */
@media (max-width: 768px) {
    .rw-text-title {
        font-size: 24px;
    }
    .rw-review {
        font-size: 18px;
        line-height: 1.6rem;
        letter-spacing: 1.2px;
        -webkit-line-clamp: 3; /* Keep it readable without squishing */
    }
    .rw-text-desc {
        font-size: 28px;
    }
    .rw-text-format {
        font-size: 18px;
    }
    .rw-reviewer-name {
