.form-container {
    padding: 32px;
    display: flex;
    flex-direction: column;
    grid-gap: 40px;
    gap: 40px;
    background: linear-gradient(to right, #ECF2FF, #FBFCFF);
}
.form-title {
    font-family: 'Poppins', sans-serif;
    font-size: 32px;
    font-weight: bold;
}
.form-title > span {
    position: relative;
}
.form-title span::before {
    content: '';
    position: absolute;
    width: 75%;
    height: 4px;
    background-color: #795484;
    border-radius: 8px;
    left: 0;
    bottom: -12px;
}
/* Form */
.form-content label {
    margin: 0 0 16px 0;
    display: block;
    font-family: 'Rubik', sans-serif;
    font-size: 24px;
    letter-spacing: .7px;
}
.form-content label.required::before {
    content: "*";
    color: red;
    padding: 0 4px 0 0;
}
.form-content label :is(input, select) {
    width: 100%;
    margin: 8px 0;
    padding: 0 12px;
    display: block;
    height: 40px;
    color: black;
    border: 1px solid transparent;
    border-radius: 6px;
    background-color: #c9def4;
    outline: transparent;
    font-family: 'Rubik', sans-serif;
    font-size: 18px;
    letter-spacing: .7px;
}
/* Success Message */
.success-message {
    margin: 20px 0 0 0;
    color: green;
    font-family: 'Rubik', sans-serif;
    font-size: 22px;
    letter-spacing: .7px;
    line-height: 1.7rem;
}
/* Error Message */
.error-message {
    color: red;
    font-family: 'Rubik', sans-serif;
    font-size: 16px;
    letter-spacing: .7px;
    line-height: 1.7rem;
}
.custom-button {
    background-color: #795484; /* Vibrant color */
    color: white;
    padding: 12px 24px;
    font-size: 16px;
    font-weight: bold;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
}

.custom-button:hover {
    background-color:#694773; /* Darker shade on hover */
    transform: scale(1.05); /* Slight zoom effect */
}

.custom-button:active {
    background-color: #694773;
    transform: scale(0.98); /* Subtle click effect */
}

/* Responsive */
@media screen and (max-width: 600px) {
    .form-container {
        padding: 24px;
    }
    .form-title {
        font-size: 24px;
    }
    .form-content label {
        font-size: 18px;
    }
    .form-content label :is(input, select) {
        font-size: 16px;
    }
    .success-message {
        margin: 12px 0 0 0;
        font-size: 18px;
    }
}

